.App{
  text-align: center;
  height: 100vh;
  display: flex;
  justify-content: center;
  margin-top: 8%;
  
}
.box{
  width:410px;
  height:530px;
  border: 3px solid black;
  border-radius: 5px;
  box-shadow: 2px 2px 2px 2px;

}
#header{
  font-size: 2rem;
  color: crimson;
}
.display{
  padding-bottom: 10px;
  padding-top: 10px;
  padding-right: 10px;
  margin-left: 20px;
  width: 360px;
  resize: none;
  border: 1px solid blue;
  border-radius: 5px;
  background-color: aqua;
  cursor: pointer;
  text-align: right;
  font-size: 18px;
}
.display .show{
  margin-bottom: 30px;
}
.result{
  background-color: aqua;
  width: 460px;
  border-radius: 0 0 5px 5px;

}

@media screen and (max-width: 460px) {
  .box{
    width: 80%;
  }
  .display{
    width: 85%;
  }
}
@media screen and (max-width: 420px){
  #header{
    font-size: large;
  }
}
