.total{
    display: flex;
}
.row1 .digit,.row2 .digit,.row3 .digit,.row4 .digit{
    width: 60px;
    height: 50px;
    border: 2px solid brown;
    border-radius: 2px;
    display: inline-block;
    margin-left: 10px;
    margin-top: 10px;
    padding-top: 10px;
    align-items: left;
    cursor: pointer;
    background-color:ivory;
    box-shadow: 1px 1px 0 0;
}
.row1{
    margin-top: 20px;
    display: flex;
    margin-left: 10px;
}
.row2,.row3,.row4{
    display: flex;
    margin-left: 10px;
}
span{
    font-size: 20px;
    color: brown;
    font-weight: bold;
    padding: auto;
}
@media screen and (max-width: 460px) {
    .row1 .digit,.row2 .digit,.row3 .digit,.row4 .digit{
        width: 50px;
        height: 50px;
    }
}
@media screen and (max-width: 450px) {
    .row1 .digit,.row2 .digit,.row3 .digit,.row4 .digit{
        width: 48px;
    }
}
@media screen and (max-width: 440px) {
    .row1 .digit,.row2 .digit,.row3 .digit,.row4 .digit{
        width: 45px;
    }
}
@media screen and (max-width: 420px) {
    .row1 .digit,.row2 .digit,.row3 .digit,.row4 .digit{
        width: 43px;
    }
}
@media screen and (max-width: 402px) {
    .row1 .digit,.row2 .digit,.row3 .digit,.row4 .digit{
        width: 40px;
    }
}
@media screen and (max-width: 380px){
    .row1 .digit,.row2 .digit,.row3 .digit,.row4 .digit{
        width: 38px;
        height: 45px;
    }
    span{
        font-size: 15px;
    }
}
@media screen and (max-width: 365px){
    .row1 .digit,.row2 .digit,.row3 .digit,.row4 .digit{
        width: 35px;
    }
}
@media screen and (max-width: 345px){
    .row1 .digit,.row2 .digit,.row3 .digit,.row4 .digit{
        width: 32px;
    }
}